					<el-col :span="12">
						<el-form-item label="配送员账号" prop="peisongyuanzhanghao">
							<el-input class="list_inp" v-model="user.peisongyuanzhanghao" readonly placeholder="配送员账号" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="配送员密码" prop="peisongyuanmima">
							<el-input class="list_inp" v-model="user.peisongyuanmima" readonly placeholder="配送员密码" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="配送员姓名" prop="peisongyuanxingming">
							<el-input class="list_inp" v-model="user.peisongyuanxingming"  placeholder="配送员姓名" clearable />
						</el-form-item>
					</el-col>
					<span class="userinfo_form_btn_box">
						<el-button class='userinfo_confirm' type="primary" @click="onSubmit">保存</el-button>
					</span>
				</el-row>
			</el-form>
		</div>
	</div>
</template>

<script setup>
	import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/toolUtil";
	import {
		reactive,
		ref,
		getCurrentInstance
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const tableName = ref('peisongyuan')
	const user = ref({})
	const init = () => {
	}
	const onSubmit = () => {
		context?.$http({
			url: `${tableName.value}/update`,
			method: 'post',
			data: user.value
		}).then(res => {
			context?.$toolUtil.message('修改成功','success')
		})

	}
	const getInfo = () => {
		context?.$http({
			url: `${tableName.value}/session`,
			method: 'get'
		}).then(res => {
			user.value = res.data.data
			init()
		})
	}
	getInfo()
</script>

