	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import address from '@/views/address/list'
	import dingdanxinxi from '@/views/dingdanxinxi/list'
	import caipinxinxi from '@/views/caipinxinxi/list'
	import users from '@/views/users/list'
	import dingdanwancheng from '@/views/dingdanwancheng/list'
	import cart from '@/views/cart/list'
	import caipinfenlei from '@/views/caipinfenlei/list'
	import forum from '@/views/forum/list'
	import discusscantingxinxi from '@/views/discusscantingxinxi/list'
	import cantingxinxi from '@/views/cantingxinxi/list'
	import yonghu from '@/views/yonghu/list'
	import orders from '@/views/orders/list'
	import gonggaoxinxi from '@/views/gonggaoxinxi/list'
	import config from '@/views/config/list'
	import discusscaipinxinxi from '@/views/discusscaipinxinxi/list'
	import peisongyuan from '@/views/peisongyuan/list'
	import dingdanpeisong from '@/views/dingdanpeisong/list'
	import peisongyuanRegister from '@/views/peisongyuan/register'
	import peisongyuanCenter from '@/views/peisongyuan/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/peisongyuanCenter',
			name: '配送员个人中心',
			component: peisongyuanCenter
		}
		,{
			path: '/address',
			name: '地址',
			component: address
		}
		,{
			path: '/dingdanxinxi',
			name: '订单信息',
			component: dingdanxinxi
		}
		,{
			path: '/caipinxinxi',
			name: '菜品信息',
			component: caipinxinxi
		}
		,{
			path: '/users',
			name: '管理员',
			component: users
		}
		,{
			path: '/dingdanwancheng',
			name: '订单完成',
			component: dingdanwancheng
		}
		,{
			path: '/cart',
			name: '购物车',
			component: cart
		}
		,{
			path: '/caipinfenlei',
			name: '菜品分类',
			component: caipinfenlei
		}
		,{
			path: '/forum',
			name: '我的发布',
			component: forum
		}
		,{
			path: '/discusscantingxinxi',
			name: '餐厅信息评论',
			component: discusscantingxinxi
		}
		,{
			path: '/cantingxinxi',
			name: '餐厅信息',
			component: cantingxinxi
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/orders',
			name: '订单管理',
			component: orders
		}
		,{
			path: '/gonggaoxinxi',
			name: '公告信息',
			component: gonggaoxinxi
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/discusscaipinxinxi',
			name: '菜品信息评论',
			component: discusscaipinxinxi
		}
		,{
			path: '/peisongyuan',
			name: '配送员',
			component: peisongyuan
		}
		,{
			path: '/dingdanpeisong',
			name: '订单配送',
			component: dingdanpeisong
		}
		]
	},
	{
		path: '/peisongyuanRegister',
		name: '配送员注册',
		component: peisongyuanRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
